import React,{useState} from 'react'
import TodoList from './TodoList'
const Todoitems=()=>{
    const [currentInput,setupdateInput]=useState("");
    const[Items,setItems]=useState([]);
    const itemEvent=(val)=>{
        setupdateInput(val.target.value)
    };
    const ListItems=()=>{
        setItems((listInput)=>{
            return[...listInput,currentInput];
        });
        setupdateInput("");
    };
   const deleteItems=(id)=>{
    console.log("deleted");    
    setItems((listInput)=>{
        return listInput.filter((arrElem,index)=>{
        return index!==id;
        });
      });  
    };
    return(
        <div className="form">
            <h1>ToDo_App</h1>
            <input type="text" className="inputval" placeholder="Add Items" value={currentInput} onChange={itemEvent}/>
           <button onClick={ListItems}>+</button>
            <div className="todo_container">
            <ul className="todo_list">
                {Items.map((itemval,index)=>{
                   return <TodoList  
                   key={index} 
                   id={index} 
                   onSelect={deleteItems} 
                   text={itemval}/>;
                })}
            </ul>
        </div>
        </div>
    );
};   
export default Todoitems;